export const parseAttribute = <T>(element: HTMLElement, attributeName: string, defaultValue: T): T => {
  const attribute = element.getAttribute(attributeName);
  const stringifiedAttributeValue =
    attribute !== null
      ? ([attribute, defaultValue].find((item) => typeof item === 'string') as string | undefined)
      : undefined;
  return stringifiedAttributeValue !== undefined
    ? (JSON.parse(stringifiedAttributeValue.replace(/&quot;/g, '"')) as T)
    : defaultValue;
};
